.homepage{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:20px 80px;
}

.directory-menu{
   display:flex;
   flex-wrap:wrap;
   width:100%;
   justify-content: center;
}

.menu-item{
    min-width:30%;
    height:240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border:1px solid #000;
    margin: 0 7.5px 15px;

    &:first-child{
        margin-right: 7.5px;
    }

    &:last-child{
        margin-left: 7.5px;
    }

    .content{
        height: 90px;
        padding:0 25px;
        display: flex;
        flex-direction:column;
        align-items: center;
        justify-content: center;
        border:1px solid #000;
        
        .title{
          font-weight: bold;
          margin-bottom: 6px;
          font-size:22px;
          color : #464646;
        }
        
        .subtitle{
            font-weight: lighter;
            font-size:16px;
        
        }
    }
}